/* ------------------------------------*\
    $SITE MAIN
\*------------------------------------ */

body.admin-bar {
  .is-fixed {
    top: 46px;

    @include media(">=782px") {
      top: 32px;
    }
  }

  .is-sticky {
    top: 86px;

    @include media(">=782px") {
      top: 72px;
    }
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  background: $background-color;
  font: 400 100% / 1.3 $font-primary;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $body-color;
  overflow-x: hidden;
}

.main {
  padding-top: rem(80);
  padding-bottom: $space * 4;

  @include media(">large") {
    padding-top: rem(100);
  }
}

.has-sticky-button {
  &::after {
    content: "";
    height: 80px;
    width: 100%;
    position: relative;
    display: block;
    z-index: 0;
    pointer-events: none;
  }
}



